@import url(https://fonts.googleapis.com/css?family=Kanit:900);
@import url(https://fonts.googleapis.com/css?family=Open+Sans|PT+Serif);
/*
0 - 600px:      Phone
600 - 900px:    Tablet portrait
900 - 1200px:   Tablet landscape
[1200 - 1800] is where our normal styles apply
1800px + :      Big desktop
$breakpoint arguement choices:
- phone
- tab-port
- tab-land
- big-desktop
ORDER: Base + typography > general layout + grid > page layout > components
1em = 16px
*/
* {
  margin: 0;
  padding: 0; }

html,
body {
  width: 100%;
  height: 100%;
  background-color: white; }

p,
.technologies li {
  font-family: "Open Sans", sans-serif;
  font-size: 20px;
  line-height: 1.8;
  color: #777; }

.logo {
  max-width: 50%; }

a.btn, a.btn.contact-btn {
  font-size: 1.3vw;
  width: 10vw;
  padding: 0.9vw 0;
  display: inline-block;
  font-weight: 700;
  background: white;
  border-radius: 4px;
  color: black;
  text-decoration: underline;
  -webkit-text-decoration-color: gray;
          text-decoration-color: gray;
  margin: 0 4px; }
  @media only screen and (max-width: 37.5em) {
    a.btn, a.btn.contact-btn {
      font-size: 5vw;
      width: 50%;
      padding: 3vw 0;
      margin: 0 auto 3vw; } }
  a.contact-btn.btn {
    text-decoration: none; }
  a.contact-btn.btn i {
    margin: 5px; }
    @media only screen and (max-width: 37.5em) {
      a.contact-btn.btn i {
        position: absolute;
        left: 100px; } }

a.btn:focus {
  outline: none !important;
  box-shadow: none; }

.buttons {
  padding: 7vh 0 0; }
  @media only screen and (max-width: 37.5em) {
    .buttons {
      margin: 0 40px 0 0; } }

.style-particles {
  position: absolute;
  top: 0%;
  left: 0%;
  width: 100%;
  height: 100%; }

.cover, .contact-me-banner {
  height: 100vh;
  position: relative; }

.banner-content {
  position: absolute;
  -webkit-transform: translate(0px, -50%);
          transform: translate(0px, -50%);
  top: 40%;
  width: 60vw;
  margin: 0 12% 0;
  z-index: 1; }

.banner-title, .contact-me-title {
  margin-top: 5vw;
  font-size: 4.5vw;
  font-family: "PT Serif", serif;
  font-weight: 700; }
  @media only screen and (max-width: 37.5em) {
