      font-size: 10vw;
      margin-top: 10vw; } }

.banner-description {
  font-size: 1.5rem;
  padding: 1.5vh 0 0; }

.contact-me-banner {
  background: linear-gradient(rgba(70, 82, 214, 0.3), rgba(14, 20, 83, 0.5)), url(/portfolio/static/media/keyboard.17c001f0.jpeg);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center; }
  .contact-me-banner ul {
    display: inline-block;
    position: relative;
    overflow: hidden;
    list-style: none;
    text-align: center;
    padding: 6.5vh 0 0;
    margin: 0; }
  .contact-me-banner ul li {
    display: inline-block;
    list-style: none;
    padding: 0; }
  .contact-me-banner ul li a {
    padding: 2vw 2.5vw;
    display: inline-block;
    vertical-align: middle;
    box-shadow: 0 0 1px transparent;
    -webkit-transform: perspective(1px) translateZ(0);
            transform: perspective(1px) translateZ(0); }
  .contact-me-banner ul li a i {
    color: white; }

.contact {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }

.contact-me-title {
  font-size: 6.5vw; }
  @media only screen and (max-width: 37.5em) {
    .contact-me-title {
      font-size: 10vw;
      margin-top: 10vw; } }

.subtitle {
  padding: 1.5vh 0 0; }

.contact-me-title,
.subtitle {
  color: white; }

.standing {
  position: absolute;
  width: 15%;
  right: 10%;
  bottom: 10%; }

@media only screen and (max-width: 37.5em) {
  .banner-content .contact {
    width: 0; } }

/*PROJECTS*/
.main-content {
  max-width: 90%;
  margin: auto;
  padding-bottom: 50px;
  margin-top: 200px; }
  @media only screen and (max-width: 37.5em) {
    .main-content {
      margin-top: 0; } }

.container {
  margin: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap; }

.project {
  margin-bottom: 100px;
  width: 40%; }
  @media only screen and (max-width: 37.5em) {
    .project {
      margin-bottom: 80px;
      width: 100%;
      height: 50%; } }
  .project .image {
    height: 540px;
    width: 100%;
    padding: 30px;
    background-color: #eee;
    border-radius: 8px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    position: relative;
    min-height: 500px; }
    @media only screen and (max-width: 37.5em) {
      .project .image {
        min-height: 200px;
        height: 100%;
        padding: 10px; } }
    .project .imageimg {
      transition: -webkit-filter 0.3s ease;
      transition: filter 0.3s ease;
      transition: filter 0.3s ease, -webkit-filter 0.3s ease; }

.project .image .hover {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 10;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 22px;
  border-radius: 8px;
  font-weight: 700;
  opacity: 0;
  transition: opacity 0.2s ease; }
  .project .image .hover:hover {
    opacity: 1; }

.project .image:hover {
  overflow: hidden; }
  .project .image:hover img {
    -webkit-filter: blur(30px);
            filter: blur(30px); }

.card-details .card-title {
